
import { defineComponent } from "vue";
import { EditTwoTone } from "@ant-design/icons-vue";
import { useRouter } from "vue-router";

export default defineComponent({
  name: "GoPublish",
  components: {
    EditTwoTone,
  },
  setup() {
    const router = useRouter();
    const goPublish = () => {
      router.push({
        name: "Publish",
      });
    };
    return {
      goPublish,
    };
  },
});
